import React from 'react';
import { makeStyles, Grid, List, ListItem, ListItemText, FormGroup, FormControlLabel, Checkbox, TextField, Button } from '@material-ui/core';
import { DataGrid } from '@material-ui/data-grid';
import Header from './Header'
import Footer from './Footer'

import './admin.css';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

export default function EventSingle() {
  const classes = useStyles();


  function ListItemLink(props) {
    return <ListItem button component="a" {...props} />;
  }
  const columns = [

    // { field: 'id', flex: 1, headerName: 'S.No.', width: 100 },
    {
      field: 'chips',
      headerName: 'Chips',
      editable: true,
      flex: 1,
    },
    {
      field: 'description',
      headerName: 'Free Chips',
      editable: true,
      flex: 1,
    },
    {
      field: 'liability',
      headerName: 'Liability',
      editable: true,
      flex: 1,
    },
    {
      field: 'wallet',
      headerName: 'Wallet',
      editable: true,
      flex: 1,
    },
    {
      field: 'up',
      headerName: 'Up',
      editable: true,
      flex: 1,
    },
    {
      field: 'down',
      headerName: 'Down',
      editable: true,
      flex: 1,
    },
  ];

  const rows = [
    { id: 1, chips: '', description: '0', liability: '00', wallet: '0.00', up: '0.00', down: '0.00', },
  ];

  return (
    <div className={classes.root}>
      <Header />
      <Grid className="bodypart custom-container grid-row">
        <Grid item lg={2} xs={2} className="xs_none tl-hide">
          <List component="nav" className="side_list" aria-label="secondary mailbox folders">
            <ListItemLink href="#simple-list" className="active">
              <ListItemText primary="Sports" />
            </ListItemLink>
            <ListItemLink href="#simple-list">
              <ListItemText primary="In-Play" />
            </ListItemLink>
            <ListItemLink href="#simple-list">
              <ListItemText primary="Cricket" />
            </ListItemLink>
          </List>
        </Grid>

        <Grid item lg={10} xs={12} className="report-section">
          <div style={{ height: 400, width: '100%' }}>
            <div className="datatable_heading">
              Account Info
            </div>
            <div className="datatable_filter">
              <div className="datatable_check">
                <FormGroup row>
                  <FormControlLabel
                    control={<Checkbox name="checkedA" color="primary" />}
                    label="All"
                  />
                  <FormControlLabel
                    control={<Checkbox name="checkedA" color="primary" />}
                    label="Free Chips"
                  />
                  <FormControlLabel
                    control={<Checkbox name="checkedA" color="primary" />}
                    label="Settlement"
                  />
                  <FormControlLabel
                    control={<Checkbox name="checkedA" color="primary" />}
                    label="Profit Loss"
                  />
                  <FormControlLabel
                    control={<Checkbox name="checkedA" color="primary" />}
                    label="Statement"
                  />
                </FormGroup>
              </div>
              <div className="search_filter">
                <TextField
                  id="date"
                  label="From Date"
                  type="date"
                  className={classes.textField}
                  InputLabelProps={{
                    shrink: true,
                  }}
                />
                <TextField
                  id="date"
                  label="To Date"
                  type="date"
                  className={classes.textField}
                  InputLabelProps={{
                    shrink: true,
                  }}
                />
                <input type="text" placeholder="Search" />
                <Button variant="contained" color="primary">
                  Filter
                </Button>
                <Button variant="contained" color="secondary">
                  Clear
                </Button>
              </div>
            </div>
            <DataGrid
              rows={rows}
              columns={columns}
              pageSize={5}
              disableSelectionOnClick
            />
          </div>
        </Grid>
      </Grid>

      <Footer />
    </div >
  );
}