import React from 'react'
import './admin.css'
import './adminResponsive.css'

import InPlay from '../assets/in-play.png';
import AddUser from '../assets/add-user.png';
import Home4 from '../assets/home-4.png';
import Timer from '../assets/timer.png';
import Logout from '../assets/logout.png';

import { makeStyles, BottomNavigation, BottomNavigationAction } from '@material-ui/core'

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
}));

const Footer = () => {

    const classes = useStyles();

    const [nav, setNav] = React.useState(2);

    return(
        <>
            <div className="bottom_nav">
                <BottomNavigation
                    value={nav}
                    onChange={(event, newValue) => {
                        setNav(newValue);
                    }}
                    showLabels
                    className={classes.root}
                >
                    <BottomNavigationAction label="Inplay" icon={<img alt="sgBetImage" src={InPlay} />} className="Bmenubtn"/>
                    <BottomNavigationAction label="Users" icon={<img alt="sgBetImage" src={AddUser} />} className="Bmenubtn"/>
                    <BottomNavigationAction label="HOME" icon={<img alt="sgBetImage" src={Home4} />} className="Bmenubtn"/>
                    <BottomNavigationAction label="Bet History" icon={<img alt="sgBetImage" src={Timer} />} className="Bmenubtn"/>
                    <BottomNavigationAction label="Logout" icon={<img alt="sgBetImage" src={Logout} />} className="Bmenubtn"/>
                </BottomNavigation>
            </div>
        </>
    )
}

export default Footer;