import React from 'react'

import { Grid, Button, Link, Card, CardActionArea, CardContent, Typography } from '@material-ui/core'

import open_teen from '../assets/open-teen.jpg';

const MiniGames = () => {


    return (
        <div>
            <Grid container spacing={1}>
                <Grid item sm={12} md={12} xs={12} lg={12}>
                    <Card className="mini-games">
                        <CardActionArea>
                            <img alt="sgBetImage" src={open_teen} />
                            <CardContent className="mini-games-content">
                                <Typography>
                                    <Link href="#" className="mini-games-link">
                                        <Button variant="contained" color="primary" className="mini-games-btn" >
                                            Cricket
                                        </Button><p>Play Now</p>
                                    </Link>
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </Grid>
            </Grid>
        </div>
    )
}

export default MiniGames;