import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';

import Logo from './assets/logo.png'
import greenexchLogo from './assets/greenexch.png'
import { FormControl, Input, FormControlLabel, Checkbox, Button } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function TransitionsModal() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button variant="contained" className="signIn-btn" onClick={handleOpen}>Sign In</Button>
      <Modal
        // aria-labelledby="transition-modal-title"
        // aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes}>
            {/* <h2 id="transition-modal-title"><span className="greentxt">Green</span>exch</h2>
            <p id="transition-modal-description">Welcome, Please Sign In to Play</p> */}
            <div className="login-outer">
              <div>
                <div className="login-body">
                    <div id="wrapper">
                        <span className="close">X</span> 
                        <div className="logo">
                            <img src={greenexchLogo} />
                        </div>
                        <div id="login" className="form">
                            <section className="login-form">
                                <form>
                                    <FormControl fullWidth className="login-margin" >
                                      <label> Username
                                          <Input placeholder="Enter username" />
                                      </label>
                                    </FormControl>
                                    <FormControl fullWidth className="login-margin">
                                      <label> Password
                                        <Input placeholder="Password" />
                                      </label>
                                    </FormControl>
                                    <FormControlLabel className="remember-me login-margin"
                                        control={<Checkbox name="checkedB" color="primary" />} label="Remember me" />
                                    <FormControl fullWidth >
                                        <Button variant="contained" color="primary" size="large" className="login-btn">SIGN IN</Button>
                                    </FormControl>
                                    <span className="white-txt align-center sp-10">Easy to Place Bets on Multiple Sports With our Mobile Application</span>
                                    <FormControl fullWidth >
                                        <Button variant="contained" color="primary" size="large" className="download-btn">Download Apk</Button>
                                    </FormControl>
                                </form>
                            </section>
                        </div>
                    </div>
                </div>
            </div>
            </div>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
