import React from 'react';
import { makeStyles, Grid, List, ListItem, ListItemText, FormControl, NativeSelect, Button } from '@material-ui/core';
import { DataGrid } from '@material-ui/data-grid';
import Header from './Header'
import Footer from './Footer'

import './admin.css';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

export default function EventSingle() {
  const classes = useStyles();


  function ListItemLink(props) {
    return <ListItem button component="a" {...props} />;
  }
  const columns = [

    // { field: 'id', flex: 1, headerName: 'S.No.', width: 100 },
    {
      field: 'chips',
      headerName: 'Chips',
      editable: true,
      flex: 1,
    },
    {
      field: 'description',
      headerName: 'Free Chips',
      editable: true,
      flex: 1,
    },
    {
      field: 'liability',
      headerName: 'Liability',
      editable: true,
      flex: 1,
    },
    {
      field: 'wallet',
      headerName: 'Wallet',
      editable: true,
      flex: 1,
    },
    {
      field: 'up',
      headerName: 'Up',
      editable: true,
      flex: 1,
    },
    {
      field: 'down',
      headerName: 'Down',
      editable: true,
      flex: 1,
    },
  ];

  const rows = [
    { id: 1, chips: '', description: '0', liability: '00', wallet: '0.00', up: '0.00', down: '0.00', },
  ];

  const [state, setState] = React.useState({
    age: '',
    name: 'hai',
  });

  const handleChange = (event) => {
    const name = event.target.name;
    setState({
      ...state,
      [name]: event.target.value,
    });
  };

  return (
    <div className={classes.root}>
      <Header />
      <Grid className="bodypart custom-container grid-row">
        <Grid item lg={2} xs={2} className="xs_none tl-hide">
          <List component="nav" className="side_list" aria-label="secondary mailbox folders">
            <ListItemLink href="#simple-list" className="active">
              <ListItemText primary="Sports" />
            </ListItemLink>
            <ListItemLink href="#simple-list">
              <ListItemText primary="In-Play" />
            </ListItemLink>
            <ListItemLink href="#simple-list">
              <ListItemText primary="Cricket" />
            </ListItemLink>
          </List>
        </Grid>

        <Grid item lg={10} xs={12} className="report-section">
          <div style={{ height: 400, width: '100%' }}>
            <div className="datatable_heading">
              Profit Loss Listing &nbsp;
              <FormControl className={classes.formControl}>
                <NativeSelect
                  value={state.age}
                  onChange={handleChange}
                  name="age"
                  className={classes.selectEmpty}
                  inputProps={{ 'aria-label': 'age' }}
                >
                  <option value={10}>10</option>
                  <option value={25}>25</option>
                  <option value={50}>50</option>
                  <option value={100}>100</option>
                  <option value={500}>500</option>
                </NativeSelect>
              </FormControl>
            </div>
            <div className="datatable_filter profit_filter">
              <FormControl className={classes.formControl}>
                <NativeSelect
                  value={state.age}
                  onChange={handleChange}
                  name="age"
                  className={classes.selectEmpty}
                  inputProps={{ 'aria-label': 'age' }}
                >
                  <option value={0}>All</option>
                  <option value={25}>Cricket</option>
                  <option value={50}>Soccer</option>
                  <option value={100}>Tennis</option>
                  <option value={500}>Live teenpatti</option>
                  <option value={500}>Live Casino</option>
                  <option value={500}>Live Game</option>
                  <option value={500}>Fancy</option>
                </NativeSelect>
              </FormControl>
              <input type="date" />
              <input type="date" />
              <input type="text" placeholder="Via event name" />
              <Button variant="contained" color="primary">
                Filter
              </Button>
              <Button variant="contained" color="secondary">
                Clear
              </Button>
            </div>
            <DataGrid
              rows={rows}
              columns={columns}
              pageSize={5}
              disableSelectionOnClick
            />
          </div>
        </Grid>
      </Grid>
      <Footer />

    </div >
  );
}